@charset "UTF-8";
/** Общее **/
html,
body {
  height: 100%;
  min-width: 240px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400; }

#wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

#content {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column; }

#footer {
  flex: 0 0 auto; }

a,
a:active,
a:link,
a:visited,
a:focus {
  color: #048dcd;
  transition: color 0.3s ease 0s; }

a:hover {
  text-decoration: none;
  color: #4C6B8F; }

.account {
  margin-top: 15px; }
  .account__divider {
    margin: 0 5px;
    color: #838383; }

.panel {
  margin-top: 15px; }
  .panel__mainTable {
    margin: 0 auto; }
